.intro { background-image: url("../img/intro/bg.png"); background-size: cover; height: 900px; position: relative; }

.intro h1 { font-size: 85px; letter-spacing: 2.1px; color: #ffffff; text-align: center; font-family: 'Austin'; }

.intro h1 span { font-size: 60px; letter-spacing: 1.5px; display: block; }

.intro h1 i { color: #7c79fc; font-style: normal; border-bottom: 2px dashed #7c79fc; }

.intro a.free { display: block; width: 566px; height: 112px; border-radius: 51px; background-color: #ffb257; position: absolute; left: 50%; transform: translateX(-50%); bottom: 130px; text-decoration: none; padding-left: 50px; font-family: 'Circe'; }

.intro a.free span { font-size: 18px; line-height: 1.33; letter-spacing: 1.4px; text-align: left; color: #000000; text-shadow: 0px -1px 2px rgba(255, 255, 255, 0.55); text-transform: uppercase; display: flex; align-items: center; height: 112px; }

.intro a.free img { position: absolute; width: 202px; height: 200px; bottom: 0; right: 15px; }

.intro__course { width: 980px; height: 160px; border-radius: 20px; background-color: #1d1d1d; position: absolute; z-index: 10; bottom: -80px; left: 50%; transform: translateX(-50%); display: flex; align-items: center; }

.intro__course h2 { font-family: Circe; font-size: 28px; letter-spacing: 2.8px; text-transform: uppercase; }

.intro__course h2 span { font-size: 13.5px; line-height: 1.44; letter-spacing: 1.4px; text-align: center; color: #aaaaaa; }

.intro__course ul li { font-size: 14px; letter-spacing: 1.1px; text-align: left; color: #ebebeb; position: relative; }

.intro__course ul li:not(:first-child) { display: inline-block; padding-right: 20px; }

.intro__course ul li:before { content: ''; background: url("../img/course/dot.png"); display: block; width: 7px; height: 5px; position: absolute; left: -15px; top: 7px; }

.intro__course__block { display: block; background-image: url("../img/course/bg.gif"); background-size: cover; background-position: 70px; position: absolute; width: 300px; height: 160px; top: 0; right: 0; border-radius: 18px; display: flex; justify-content: center; align-items: center; flex-direction: column; text-decoration: none; }

.intro__course__block img, .intro__course__block span { position: relative; z-index: 10; }

.intro__course__block span { color: #ffaf6a; }

.intro__course__block__black { position: absolute; width: 100%; height: 100%; background: rgba(0, 0, 0, 0.5); top: 0; border-radius: 18px; }

.intro__course__block + span { display: none; }

.mountain { background-image: url("../img/mountain/bg.png"); background-size: cover; background-repeat: no-repeat; background-position: 50% 50%; width: 100%; height: 940px; position: relative; }

.mountain h2 { color: #ffffff; text-shadow: 0px 9px 12.1px rgba(0, 0, 0, 0.2); margin-top: 130px; text-align: center; }

.mountain h2 span { color: #ffffff; text-shadow: 0px 9px 12.1px rgba(0, 0, 0, 0.2); text-align: center; }

.mountain__arrow { background-image: url("../img/mountain/arrow.png"); background-repeat: no-repeat; width: 500px; height: 400px; display: block; position: absolute; bottom: 100px; left: 51%; transform: translateX(-50%); }

